<template>
  <div class="audio-bar">

        <my-canvas class="canvas"
            ref="myCanvas" audioTag="111"
                      @pauseAudio="pauseTarget"
                      @playAudio="playTarget"/>
      </div>
      <div class="audio-bar">
        <div class="sample-name"></div>
      <div class="desc" ref="desc1">VampNet</div>
        <div class="desc" ref="desc2">MusicGen</div>
        <div class="desc" ref="desc3">Drums-AIR</div>
        <div class="desc" ref="desc4">Chord-AIR</div>
        <div class="desc" ref="desc5">Piano-AIR</div>
      </div>
      <div v-for="j in jdx" :key="j">
        <div class="audio-bar">
          <div class="sample-name" :ref="'sample'+j">{{"Sample " + ("00" + j).slice(-3)}}</div>

          <my-audio class="audio"
              :musicSrc="audioSrc[idx][j - 1][mask_id][k - 1]"
                  :audioTag="[j, k]"
                  :ref="''+j+k"
                    @play="updateAttrs(j, k)"
                    @timeupdate="updateCanvas"
                  v-for="k in kdx" :key="''+j+k"
                  />
          </div>
      </div>

</template>


<script>
import MyAudio from './MyAudio.vue'
import MyCanvas from './MyCanvas.vue'

function generate_file_list(folder, n) {
  let prefix = "./audio/" ;
  let outs = Array(0);
  for(let i = 1; i <=2; ++ i){
    let res = Array(0);
    for(let j = 1; j <=n; ++ j){
      let t_res = Array(0);
      let folder_name = prefix + folder + i + "/" +  ("00" + j).slice(-3) + "/";
      for(let k=0; k <=1;++k){
        let t = Array(0);
        t.push(folder_name + "vampnet_mask_" + k + "_full_0.wav");
        t.push(folder_name + "musicgen_mask_" + k + "_full_0.wav.wav");
        t.push(folder_name + "fix_drums_mask_" + k + "_full_0.wav");
        t.push(folder_name + "chord_mask_" + k + "_full_0.wav");
        t.push(folder_name + "piano_mask_" + k + "_full_0.wav");
        t_res.push(t);
      }
      res.push(t_res);
    }
    outs.push(res)
  }

  return outs;
}

export default {
  name: 'AudioGroup',
  components: {
    MyAudio, MyCanvas
  },
  props: {
    idx:{
      type: Number,
      default: 1,
    },
    jdx:{
      type: Number,
      default: 3,
    },
    kdx:{
      type: Number,
      default: 3,
    },
    mask_id:{
      type: Number,
      default: 0,
    },
    audioSrc: {
      type: Array,
      default: generate_file_list("sec", 15),
    }

  },
  methods:{
    updateAttrs(sj, sk){
      this.reviseDiv(sj, sk);
      let target = "" + sj +sk;

      let pgs = this.$refs[target][0].getProgress();
      this.reviseCanvas(pgs[0], sj, sk, 0, pgs[1], pgs[2]);

      this.pauseAll(target);
    },
    reviseDiv(sj, sk){
      this.$refs.myCanvas.globalInit = true
      this.$refs["desc"+sk].style.background = "rgba(255, 0, 0, 0.1)";
      this.$refs["sample"+sj][0].style.background = "rgba(255, 0, 0, 0.1)";
      for(let k = 1; k<=this.kdx; k++){
        if(k !== sk )
          this.$refs["desc"+k].style.background = "rgba(255, 0, 0, 0)";
      }
      for(let j = 1; j<=this.jdx; j++){
        if(j !== sj )
          this.$refs["sample"+j][0].style.background = "rgba(255, 0, 0, 0)";
      }
    },
    reviseCanvas(pg, sj, sk, s, cur, dur){
      let target = "" + sj +sk;
      let tag_sample = "00" + sj + "/00" + (sk - 1);
      let tag_ref = "00" + sj + "/000";
      let qlen = Math.ceil(750 / 2);
      let hqlen = Math.ceil(qlen / 2);
      let hhlen = Math.ceil(hqlen / 2);
      qlen = qlen / 1501 * 15.;
      hqlen = hqlen / 1501* 15.;
      hhlen = hhlen / 1501* 15.;

      let mask_1 = [[[0, qlen], [qlen * 3, 15.]],
                [[0, qlen], [qlen * 3, 15.]],
                [[0, hqlen], [hqlen + qlen, hqlen + qlen * 2], [hqlen + qlen * 3, 15.]],
                [[0, hqlen], [hqlen + qlen, hqlen + qlen * 2], [hqlen + qlen * 3, 15.]],
                [[0, hhlen], [hqlen + hhlen, hqlen * 2 + hhlen],
                  [hqlen * 3 + hhlen, hqlen * 4 + hhlen],
                  [hqlen * 5 + hhlen, hqlen * 6 + hhlen],
                  [hqlen * 7 + hhlen, 15.]],
                [[0, hhlen], [hqlen + hhlen, hqlen * 2 + hhlen],
                  [hqlen * 3 + hhlen, hqlen * 4 + hhlen],
                  [hqlen * 5 + hhlen, hqlen * 6 + hhlen],
                  [hqlen * 7 + hhlen, 15.]]];
      let mask_2 = [
          [[0, qlen], [qlen * 3, 15.]],
                [[0, hqlen], [hqlen + qlen, hqlen + qlen * 2], [hqlen + qlen * 3, 15.]],

      [[0, qlen]], [[0, hqlen]]
      ];
      if(sk === 2){
        this.$refs.myCanvas.updateCanvas(tag_sample, tag_ref, pg, s, target, cur, dur, mask_2[this.mask_id + 2], -1);
      }else
        this.$refs.myCanvas.updateCanvas(tag_sample, tag_ref, pg, s, target, cur, dur, mask_2[this.mask_id], 15.);


    },
    updateCanvas(t, tags){
      if(typeof t[0] !== "number")
        return
      this.reviseCanvas(t[0], tags[0], tags[1], 1, t[1], t[2]);
    },
    pauseTarget(e){
      if(this.$refs[e] === undefined)
        return
      this.$refs[e][0].pause();
    },
    playTarget(e){
      if(e[0] === undefined || this.$refs[e[0]] === undefined)
        return
      this.$refs[e[0]][0].continue(e[1]);
    },
    pauseAll(target){
      for(let j = 1; j<=this.jdx; j++)
        for(let k = 1; k<=this.kdx; k++){
          let name = "" + j +k;
          if(target === null || name !== target) {
            this.$refs[name][0].pause();
          }
        }
    }
  }
}
</script>

<style scoped>

.audio-bar{
  clear: left;
  border-top: 1px solid rgba(147, 147, 147, 0.2);
  border-bottom: 1px solid rgba(147, 147, 147, 0.2);
  padding-top: 0.5%;
  padding-bottom: 0.3%;
}
.sample-name{
  float: left;
  padding: 1.5%;
  width: 8%;

}

.canvas{
  width: 95%;height: 120px;
}
.desc{
  text-align: center;
  padding: 1%;
  width: 15%;
  float: left;
}
.audio{
  width: 15%;
  padding-left:1%;
  padding-right:1%;
}

</style>
