<script>

import AudioGroup from "@/components/AudioGroup"
export default {
  name: 'App',

  components: {
    AudioGroup
  },

  methods:{
    sec1Changed(){
      if(this.$refs.sec_1_mask_1.checked){
        this.$refs.sec_1_audio_mask_1_div.hidden = false;
        this.$refs.sec_1_audio_mask_2_div.hidden = true;
        this.$refs.sec_1_audio_mask_2.pauseAll(null);
      }else{
        this.$refs.sec_1_audio_mask_1_div.hidden = true;
        this.$refs.sec_1_audio_mask_2_div.hidden = false;
        this.$refs.sec_1_audio_mask_1.pauseAll(null);
      }

    },
    sec2Changed(){
      if(this.$refs.sec_2_mask_1.checked){
        this.$refs.sec_2_audio_mask_1_div.hidden = false;
        this.$refs.sec_2_audio_mask_2_div.hidden = true;
        this.$refs.sec_2_audio_mask_2.pauseAll(null);
      }else{
        this.$refs.sec_2_audio_mask_1_div.hidden = true;
        this.$refs.sec_2_audio_mask_2_div.hidden = false;
        this.$refs.sec_2_audio_mask_1.pauseAll(null);
      }

    }
  }
}
</script>

